    :disabled='b'
    @input='phoner'
@blur='fouce'
    placeholder="请输入手机号"
  />
    <van-cell title="业务类型" 
     required is-link @click="shower" arrow-direction="down"  />
    <!-- :value="typesname" -->
    <van-cell class='celltext' title="是否为疫情防控专用需求" required is-link arrow-direction="down" @click="shower1" :value="istype" />
    <van-cell class='celltext' title="新安农商银行网点"  is-link @click="showweek" arrow-direction="down" :value="typename" />
  <van-field
    label="办理日期"
                    v-model="end_time"
                    placeholder="选择日期" readonly="readonly"
                    @click="endTimePo"
                  />
                  <van-popup v-model="endTimePop" label="选择日期" position="bottom" :overlay="true">
                    <van-datetime-picker
                      v-model="currentDate_end"
                      :min-date="minDate"
  :max-date="maxDate"
   type="date"
                       :formatter="formatter"
                      @cancel="endTimePop = false"
                      @confirm="endTim "
                      @change="endTimeChange"
                    />
                  </van-popup>
   
    <van-field
    v-model="remark"
    :disabled='adliu'
    placeholder="如果您有特殊需求，请在此留言。"
  />
    <van-action-sheet
        :close-on-click-overlay="true"
        v-model="show"
        :actions="actions"
        @select="onSelect"
      />
         <van-action-sheet
        v-model="show1"
         @click-overlay='qwer'
        :actions="actions1"
        @select="onSelect1"
      />
           <van-action-sheet
        :close-on-click-overlay="true"
         @click-overlay='qwershw'
        v-model="show2"
        :actions="actions2"
        @select="onSelect2"
      />
         <!-- <van-action-sheet
        :close-on-click-overlay="false"
        v-model="integral"
        :actions="actions3"
        @select="onSelect3"
      /> -->
</van-cell-group>  

   </div>
           <button class="login-button" @click="submit">立即提交</button>
           <div class="login-phone">
             <div class="logip-left">
               <img src="../../assets/imgs/personal/24@2x.png" style="width:20px;height:20px;margin-top:10px;margin-left:10px" alt="">
            <div style='margin-left:5px;'>服务电话:   <span style='color:#EF5157'> 15236285678</span> </div>  
           </div>
           <div class="logip-right">
            <a  href="tel:15236285678" style='color:white;'> 立即拨打>></a>
           </div>
           </div>

    
           

      
   </div>
    <!-- <van-action-sheet
        :close-on-click-overlay="false"
        v-model="integral"
        :actions="actions3"
        @select="onSelect3"
      /> -->
           <van-dialog
      v-model="dzshow"
      title="电子银行"
      showCancelButton
      confirmButtonColor="#DE4F53"
            @confirm="dzpay"
      @cancel="dzcancelPay"
    >
      <van-checkbox-group v-model="dz">
  <van-checkbox name="1"  shape="square">开通手机银行</van-checkbox>
  <van-checkbox name="2"  shape="square">开通网上银行</van-checkbox>
  <van-checkbox name="3"  shape="square">变更手机号</van-checkbox>
  <van-checkbox name="4"  shape="square">重置登录密码</van-checkbox>
  <van-checkbox name="5"  shape="square">吞卡处理</van-checkbox>
</van-checkbox-group>
      <van-cell-group>
     
        <!-- <van-field
          v-model="myIntegral"
          :label="typesname"
    placeholder="请输入"
          input-align="right"
        /> -->
      </van-cell-group>
    </van-dialog>
     <van-dialog
      v-model="integralShow"
      title="贷款"
      showCancelButton
      confirmButtonColor="#DE4F53"
            @confirm="pay"
      @cancel="cancelPay"
    >
      <van-cell-group>
       
  <div>
    金额
  </div>
        
          <van-radio-group v-model="je">
  <van-radio name="1" shape="square">10万以内</van-radio>
  <van-radio name="2" shape="square">10万以上30万以内</van-radio>
  <van-radio name="3" shape="square">30万以上</van-radio>
</van-radio-group>
 <div>
    用途
  </div>
        <van-radio-group v-model="yt">
  <van-radio name="1" shape="square">经营类</van-radio>
  <van-radio name="2" shape="square">消费类</van-radio>
  <van-radio name="3" shape="square">其他</van-radio>
</van-radio-group>
      </van-cell-group>
    </van-dialog>

<van-dialog
      v-model="cunkuanshow"
      class="cunkuan"
      title="存款"
      showCancelButton
      confirmButtonColor="#DE4F53"
            @confirm="cunkuanpay"
      @cancel="cunkuancancelPay"
    >
      <van-cell-group>
        <van-field
          v-model="cunkuan"
    placeholder="请输入金额"
        />
    <div style="">元</div>
      </van-cell-group>
    </van-dialog>
  <van-dialog
      v-model="qukuanshow"
      title="取款"
      class="cunkuan"
      showCancelButton
      confirmButtonColor="#DE4F53"
            @confirm="qukuanpay"
      @cancel="qukuancancelPay"
    >
      <van-cell-group>
        <van-field
          v-model="qukuan"
    placeholder="请输入金额"
        />
    <div style="">元</div>

      </van-cell-group>
    </van-dialog>

  <van-dialog
      v-model="qitashow"
      title="其他"
      class="cunkuan"
      showCancelButton
      confirmButtonColor="#DE4F53"
            @confirm="qitapay"
      @cancel="qitacancelPay"
    >
      <van-cell-group>
        <van-field
          v-model="qita"
    placeholder="请输入"
        />
      </van-cell-group>
    </van-dialog>

           <div class="bott">
             
    <van-swipe class="my-swipe" @change="onChange"  :loop='false' >
  <van-swipe-item>
               <img src="../../assets/imgs/personal/01.png"  alt="">

  </van-swipe-item>
  <van-swipe-item>
               <img src="../../assets/imgs/personal/02.png"  alt="">

  </van-swipe-item>
  <van-swipe-item>
               <img src="../../assets/imgs/personal/03.png"  alt="">

  </van-swipe-item>
  <van-swipe-item>
               <img src="../../assets/imgs/personal/04.png"  alt="">

  </van-swipe-item>
    <div class="custom-indicator" slot="indicator">
    {{ current + 1 }}/4
  </div>
</van-swipe>
           </div>
