</template>

<script>
import { apply,bankList } from "@/api/login";
import { Toast } from 'vant';
export default {
  data() {
    return {
              checked: true,
  current: 0,
               minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate:false,
      b:true,
      c:true,
      radio:'1',
      endTimePop:false,
      integral:false,
      integralShow:false,
      qita:'',
      je:'',
      yt:'',
      qitashow:false,
        dz:[],
      dzshow:false,
       qukuan:'',
      qukuanshow:false,
     cunkuan:'',
      cunkuanshow:false,
      is_default:1,
              typename:'',
              typesname:'',
              myIntegral:"",
              integralInput:"",
              istype:"",
              name:"",
              phone:"",
              end_time:"",
              remark:"",
              currentDate_end:"",
        temp:{
            member:"",
            password:"",
        },
      activeIndex: 0, //规格默认
      slides: [],
      goodsValue: 1,
      goodsId: "",
      goodsInfo: {},
      freight: "",
      deposit:"",
      get:"",
      decript:"",
      loan:"",
      use_loan:"",
      back_id:"",
      bank_type:"",
      backlist:[],
      goodsSize: [],
      list:[],
      chooseSize: {},
       show: false,
       cbd: false,
       show1: false,
       show2: false,
       id:'',
      actions: [
     
      ],
       jear: [
        { name: '10万以内' },
        { name: '10万以上30万以内' },
        { name: '30万以上' },
      ],
        ytar: [
        { name: '经营类' },
        { name: '消费类' },
        { name: '其他' },
      ],
       actions1: [
        { name: '是' },
        { name: '否' },
      ],
         actions2: [
        { name: '存款' },
        { name: '取款' },
        { name: '贷款' },
        { name: '电子银行' },
        { name: '其他' },
      ],
            actions3: [
        { name: '开通手机银行' },
        { name: '开通网上银行' },
        { name: '变更手机号' },
        { name: '重置登录密码' },
        { name: '吞卡处理' },
      ],
      reduceList: [] ,
      abc:'2',
      abcd:'2',
      adg:'2',
      ddw:'2',
      adliu:true,
      pop:[],
    }
  },
  created() {
    let arr=[]
   bankList().then(res=>{
           res.map((i,x)=>{
             arr.push({id:i.id,name:i.bank_name})
          })
          this.actions=arr
          console.log(this.actions)
          })
          
    // Toast({
    //     message: '您的业务需要已提交，请耐心等待我行人员处理',
    //     duration: 3 * 1000,
    //   });
    //   setTimeout(()=>{
    //     location.reload();
    //   },2000)
  },
  methods: {
    endTim(e){
        // let endTimeArr = e.getValues();
        // this.end_time = `${endTimeArr[0]}-${endTimeArr[1]}-${endTimeArr[2]}  `
        
// var d = new Date(e);
//  this.end_time =d.getFullYear()+ '年'  + '-' + (d.getMonth() + 1) + '月'+ '-' + d.getDate()+ '日';
       this.endTimePop=false
    },
     formatter(type, val) {
      if (type === 'year') {
        return `${val}年`;
      } else if (type === 'month') {
        return `${val}月`
      }else if (type === 'day') {
        return `${val}日`
      }
      return val;
    },
    endTimePo(){
if(this.abcd==1){
       this.endTimePop = true
     }
    },
    namer(){
      this.b=false
    },
    fouce(){
      if(this.phone==''){
             this.$toast('请输入电话号码')

      this.ddw=2
      }
 if (!(/^1[3456789]\d{9}$/.test(this.phone))) {
             this.$toast('电话号码格式错误')
      this.ddw=2
           }
    },
    phoner(){
          
      this.ddw=1
    },
     onChange(index) {
      this.current = index;
    },
    shower(){
     if(this.ddw==1){
       this.show2=true
       this.abc=1
     }
    },
    qwershw(){
       this.show2=false
      
                  this.abc=2

    },
    qwer(){
       this.show1=false
       this.abcd=2
 this.adliu=false
    },
      shower1(){
     if(this.abc==1){
       this.show1=true
       this.adliu=false
       this.abcd=1
     }
    },
    submit(){
     this.type= this.list.toString()
     this.bank_type=this.backlist.toString()
      let data={
        name:this.name,
        phone:this.phone,
        type:this.type,
        is_urgency:this.is_urgency,
        bank_branch:this.typename,
        bank_id:this.back_id,
        deposit:this.deposit,
        get:this.get,
        loan:this.loan,
        use_loan:this.use_loan,
        bank_type:this.bank_type,
        decript:this.decript,
        select_time:this.end_time,
        remark:this.remark,
      }
      apply(data).then(res=>{
        if(res.errcode==0){
             this.$toast('您的业务需要已提交，请耐心等待我行人员处理')
  setTimeout(() => {
          location.reload();
        }, 200);
        }else{
             this.$toast(res.msg)
          
        }
       
             
      })
    },
    cancelPay(){
      this.je=''
      this.yt=''
      this.integralShow = false;
                  this.abc=2
    },
     qitacancelPay(){
      this.qitashow = false;
      this.qita=''
                  this.abc=2
    },
    qitapay(){
                  this.decript= this.qita
    },
       cunkuancancelPay(){
      this.cunkuanshow = false;
      this.cunkuan=''
                  this.abc=2
    },
    cunkuanpay(){
                  this.deposit= this.cunkuan
    },
     qukuancancelPay(){
                  this.qukuanshow= false
                  this.abc=2
                  this.qukuan=''
    },
    qukuanpay(){
                  this.get= this.qukuan
    },
     dzcancelPay(){
                  this.dzshow= false
                  this.dz=[]
                  this.abc=2
    },
    dzpay(){
      // this.bank_type=this.dz.join(',')
      var art=[]
      this.dz.filter((i,x)=>{
        art.push(this.actions3[i].name)
        this.backlist=art
        
      })
                  // this.get= this.dz
    },

      pay(){

            this.loan= this.jear[this.je].name
            this.use_loan= this.ytar[this.yt].name
 
      this.integralShow = false;

    },
          delWallet(id){
    
           this.id=id
          
        
      },
       endTimeChange(e) {
        let endTimeArr = e.getValues();
        this.end_time = `${endTimeArr[0]}-${endTimeArr[1]}-${endTimeArr[2]}  `
      },
        onSelect(item) {
          bankList().then(res=>{
           res.map((i,x)=>{
             if(i.bank_name==item.name){
              this.back_id=i.id
             }
          })
          console.log(this.back_id)
          })
      this.typename = item.name;
      this.show = false;
    },
     onSelect1(item) {
      this.istype = item.name;
      if(this.istype=='否'){
          this.is_urgency=0

      }else if(this.istype=='是'){
          this.is_urgency=1
      }
      this.show1 = false;
    },
         onSelect2(item) {
      this.typesname = item.name;
      if(this.typesname=='存款'){
        this.type=1
      this.cunkuanshow = true;
      }else if(this.typesname=='取款'){
        this.type=2
      this.qukuanshow = true;
      }else if(this.typesname=='贷款'){
        this.type=3
      this.integralShow = true;
      }else if(this.typesname=='电子银行'){
        this.type=4
      this.dzshow = true;
      }else if(this.typesname=='其他'){
        this.type=5
      this.qitashow = true;
      }
     this.list.push(this.type)
     this.list=Array.from(new Set(this.list))
      this.show2 = false;
    },
            onSelect3(item) {
      if(item.name=='开通手机银行'){
        this.bank_type=1
      }else if(item.name=='开通网上银行'){
        this.bank_type=2
      }else if(item.name=='变更手机号'){
        this.bank_type=3
      }else if(item.name=='重置登录密码'){
        this.bank_type=4
      }else if(item.name=='吞卡处理'){
        this.bank_type=5
      }
      this.integral = false;
    },
    showtime(){
          this.showtime=true
      },
       showweek(){
         if(this.abcd==1){
          this.show=true
         this.adg=1
         }
      },
    //   onSelect(item) {
    //   // 默认情况下点击选项时不会自动收起
    //   // 可以通过 close-on-click-action 属性开启自动收起
    //   this.show = false;
    //   Toast(item.name);
    // },
 go(res) {
      this.$router.push({ name: res });
    },
    goPay() {
      this.$router.push({
        path: "payOrder",
        query: {
          type: 1,
        }
      });
    }
  }
};
</script>
